import { getDataJenis } from "../../../../actions/datamaster_action";
import LaporanBerlianPdf from './LaporanBerlianPdf';
import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  PanelBody,
  ReanderSelect,
  Field,
  getDataNoTimeOut,
  ToastNotification,
  Loading
} from "../../../../components/helpers/library";
import { getImage } from "../../../../config/axios";
import notFound from "../../../../assets/image-not-found.png";


const maptostate = (state) => {
  return {
    jenisBatu: state.datamaster.getDataJenis,
  };
};
class BukiBerlian extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      settings: "",
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    // this.print = this.print.bind(this);
  }

  handleSubmit(event) {
    event.preventDefault();
    this.setState({
        isLoading : true
    })
    if(this.state.jenis_batu === undefined) {
        ToastNotification('info', 'Silahakan Pilih Jenis Batu')
        this.setState({
            isLoading : false
        })
        return false
    }

    getDataNoTimeOut('berlian/buku-berlian/?kode_dept='+this.state.jenis_batu)
    .then(async(res) => {
        if (res.data.length !== 0) {
          const DataFaktur = [];
          for (const hasil of res.data) {
            await getImage('foto_produk/'+hasil.kode_barcode).then((res) => {
                hasil.gambar = res;
            }).catch((err)=>{
                hasil.gambar = notFound;
            })
            DataFaktur.push(hasil);
          }

          setTimeout(() => {
            this.setState({
              isLoading : false
            })
            LaporanBerlianPdf(DataFaktur);
          }, 200);
        }else{
          this.setState({
            isLoading : false
          })
          ToastNotification("info","Data Yang Di Cari Tidak Ada !!!")
        }
    }).catch((err) => {
        ToastNotification('info', 'Laporan Tidak Tersedia')
    })
  }

  componentDidMount() {
    this.props.dispatch(getDataJenis());
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan Berlian</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Buku Berlian</li>
        </ol>
        <h1 className="page-header">Laporan Buku Berlian</h1>
        <Panel>
          <PanelHeader>Laporan Buku Berlian</PanelHeader>
          <PanelBody>
          <form onSubmit={this.handleSubmit} autoComplete="off">
            <div className="row">
              <div className="col-lg-6">
                <Field
                  name="jenis_batu"
                  id="Kode Jenis"
                  component={ReanderSelect}
                  label="Kode Jenis"
                  onChange={(e)=>this.setState({jenis_batu : e})}
                  options={this.props.jenisBatu.map((list) => {
                    let row = {
                      value: list.kode_dept,
                      name: list.kode_dept,
                    };
                    return row;
                  })}
                  type="text"
                  placeholder="Pilih Kode Jenis"
                />
              </div>
              <div className="col-lg-6">
                <div className="text-right">
                  <label>&nbsp;</label>
                  <button
                    type="submit"
                    value="PDF"
                    disabled={this.state.isLoading}
                    name="type_btn"
                    className="btn btn-primary btn-block"
                  >
                    {this.state.isLoading ? (
                      <>
                        <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                        Mencari
                      </>
                    ) : (
                      "Cari Laporan"
                    )}
                  </button>
                </div>
              </div>
            </div>
            </form>
          </PanelBody>
        </Panel>
        {this.state.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </div>
    );
  }
}

BukiBerlian = reduxForm({
  form: "BukiBerlian",
  enableReinitialize: true,
})(BukiBerlian);
export default connect(maptostate)(BukiBerlian);
