import jsPDF from "jspdf";
import "jspdf-autotable";
import { getUserdata } from "../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanBerlianPdf = (data = "") => {
  // initialize jsPDF
  const doc = new jsPDF("P", "mm", [297, 210]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let finalY = 30;
  // let tableColumn = [];

  doc.setFontSize(15);
  doc.text("LAPORAN BUKU BERLIAN", 14, 15);
  doc.setFontSize(20);
  doc.text(getUserdata().nama_toko, 130, 15);
  doc.setFontSize(8);
  let jml_alamat = getUserdata().alamat_toko.length;
  if (jml_alamat > 20) {
    doc.text(getUserdata().alamat_toko.slice(0, 48), 130, 20);
  }
  if (jml_alamat > 50) {
    doc.text(getUserdata().alamat_toko.slice(48, 90), 130, 25);
  }
  doc.setProperties({
    title: "LAPORAN BUKU BERLIAN",
  });
  let posisi = 35;
  data.forEach((element) => {
    let row = [
      {
        content: "",
        styles: {
          cellWidth: 45,
          halign: "center",
          valign: "middle",
        },
      },
      {
        content: `
              Kode Intern  : ${element.kode_intern || "-"} \n
              Nama  : ${element.nama || "-"} \n
              Jenis  : ${element.jenis || "-"} \n
              Jumlah  : ${element.jumlah || 0} \n
              `,
      },
      {
        content: `
              \n
              \n
              \n
              Karat : ${element.karat_batu || 0}`,
      },
      // {
      //   content: `
      //       \n
      //        \n
      //       Harga Usd  : 1 \n
      //       Total Usd  : 1 \n
      //       `,
      // },
    ];
    tableRows.push(row);
    
    
    posisi = posisi + 40;
    doc.autoTable({
      // head: tableColumn,
      body: tableRows,
      didDrawCell: (row) => {
        var posisix = row.cell.x;
        var posisiy = row.cell.y;
        var dim = row.cell.height - row.cell.padding("vertical");
        if (row.cell.section === "body" && row.column.index === 0) {
          doc.addImage(element.gambar, posisix, posisiy, dim + 12, dim + 4);
        }
      },
      // foot: footRows,
      startY: finalY,
      theme: "plain",
      rowPageBreak: "avoid",
      // pageBreak: "avoid",
      margin: { top: 15 },

      footStyles: {
        fontSize: 8,
      },
      bodyStyles: {
        fontSize: 8,
        // lineWidth: 0.02,
        // lineColor: "#000",
      },
      headStyles: {
        fontSize: 8,
        lineWidth: 0.02,
        lineColor: "#000",
        // textColor: "#000",
        // fillColor: "#E8E5E5",
      },
    });
    finalY = doc.autoTableEndPosY() + 3;
  
    tableRows = [];
  });

  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left

  // doc.text(
  //   `User                        : ${
  //     JSON.parse(localStoragedecryp("userdata")).user_id
  //   }`,
  //   14,
  //   finalY + 10
  // );
  // doc.text(`Cetak                      : ${getToday()}`, 14, finalY + 16);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  // doc.autoPrint();
  // doc.save(`Laporan BARANG SUMARY.pdf`);
  var string = doc.output("datauristring");
  // var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN BUKU BERLIAN</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanBerlianPdf;